import React, {useContext, useEffect} from "react";
import axios from 'axios';

import Header from "../../widgets/Header";

import TopSection from "./TopSection";
import ShowCasePost from "./ShowCasePost";
import PostCard from "./PostCart";
import {blogContext} from "../../reducer/blogReducer";
import {mainContext} from "../../App";


const PostList = () => {
    const blog_context = useContext(blogContext);
    const main_context = useContext(mainContext);

    useEffect(()=> {
        axios.get('/api/posts/')
            .then(response=> {
                blog_context.dispatchPosts({type: "FETCH_POSTS", payload: response.data})
                // main_context.route('/posts');
            })
            .catch(error=> {
                console.log(error);
            })
    })
    return (
        <div>
            <Header active={2} nav={true} bg={'dark'} />
            <main>
                <TopSection />
                <ShowCasePost />
                <section className={"bg-light"}>
                    <div className="container pt-6 pb-7-pt-lg-7 pb-lg-10">
                        <div className="row">
                            <div className="col">
                                <h2>Latest Articles</h2>
                                {blog_context.posts.map(post=> (<PostCard post={post}/>))}
                                {/*<PostCard />*/}
                            </div>
                        </div>
                    </div>
                </section>
            </main>
        </div>
    )
}


export default PostList